.btn {
  border: none; }
  .btn:focus {
    outline: none !important; }
  .btn:hover {
    border: none; }

.btn-mini {
  height: 24px;
  font-size: 12px;
  border-radius: 12px;
  padding: 0 10px;
  margin: 2px;
  -o-box-shadow: 1px 1px 2px #818a91;
  -moz-box-shadow: 1px 1px 2px #818a91;
  -webkit-box-shadow: 1px 1px 2px #818a91;
  box-shadow: 1px 1px 2px #818a91; }
  .btn-mini.active {
    -o-box-shadow: none;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    border: none; }

.btn-success {
  background-color: #54b13c; }
  .btn-success.active {
    background-color: #399820; }

.btn-secondary.active {
  background-color: #ca4454;
  color: #ffffff; }
  .btn-secondary.active:active {
    background-color: #ca4454;
    color: #ffffff; }
  .btn-secondary.active:focus {
    background-color: #ca4454;
    color: #ffffff; }

.btn-secondary:active {
  background-color: #ffffff; }

.btn-secondary:active {
  background-color: #ca4454; }

.btn-secondary:focus {
  background-color: #ffffff; }



