.filter-price {
  background-color: #f5f5f5;
  min-height: 100px; }

.rc-slider {
  padding: 6px 0; }

.rc-slider-rail {
  height: 2px; }

.rc-slider-track {
  height: 2px;
  background-color: #54b13c; }

.rc-slider-handle {
  margin-top: -6px;
  border-color: #54b13c; }

.slider-point {
  position: absolute;
  transform: 'translate(-50%, -50%)';
  cursor: pointer;
  padding: 2px;
  border: 2px solid #abe2fb;
  border-radius: 3px;
  background: #fff;
  font-size: 14px;
  text-align: center; }

.rc-slider-tooltip-inner {
  padding: 8px 10px;
  line-height: .8;
  background-color: #54b13c; }

.rc-slider-tooltip-placement-top .rc-slider-tooltip-arrow {
  bottom: 4px;
  left: 50%;
  margin-left: -4px;
  border-width: 4px 4px 0;
  border-top-color: #54b13c; }



